/* ================= PAGE ================= */
.ticket-page {
  min-height: 100vh;
  background: radial-gradient(
    circle at top,
    #ffd2c1,
    #ffc1ad
  );
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Nunito", "Quicksand", sans-serif;
  padding: 40px 20px;
}

/* ================= WRAPPER ================= */
.ticket-wrapper {
  text-align: center;
}

/* ================= TICKET ================= */
.ticket {
  display: flex;
  background: linear-gradient(
    180deg,
    #ffe3d7,
    #ffd7c8
  );
  border-radius: 28px;
  box-shadow:
    0 28px 60px rgba(0,0,0,0.18);
  overflow: hidden;
  max-width: 760px;
  margin: 0 auto;
  animation: floatSoft 4.5s ease-in-out infinite;
}

@keyframes floatSoft {
  0%,100% { transform: translateY(0); }
  50% { transform: translateY(-6px); }
}

/* ================= LEFT ================= */
.ticket-left {
  background: linear-gradient(
    180deg,
    #ffbfa8,
    #ffad96
  );
  color: #5a2f2f;
  padding: 42px 36px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 200px;
}

.ticket-left h1 {
  font-size: 3.8rem;
  font-weight: 800;
  margin: 8px 0;
}

.label {
  font-size: 0.8rem;
  letter-spacing: 2px;
}

.small {
  font-size: 0.8rem;
  margin-top: 10px;
}

/* ================= DIVIDER ================= */
.ticket-divider {
  width: 16px;
  background:
    repeating-linear-gradient(
      to bottom,
      transparent,
      transparent 10px,
      #f2b7a4 10px,
      #f2b7a4 18px
    );
}

/* ================= RIGHT ================= */
.ticket-right {
  padding: 38px 42px;
  text-align: left;
  color: #5a2f2f;
  flex: 1;
}

.ticket-right .title {
  font-size: 2rem;
  letter-spacing: 1px;
  opacity: 0.8;
  font-family: "Dancing Script", cursive;
  margin: 6px 0 20px;
  color: #ffffff;
  text-shadow: 0 2px 6px rgba(0,0,0,0.2);
}

.ticket-right h2 {
  font-size: 3rem;
  font-family: "Dancing Script", cursive;
  margin: 6px 0 20px;
  color: #ffffff;
  text-shadow: 0 2px 6px rgba(0,0,0,0.2);
}

.details p {
  font-size: 0.95rem;
  margin-bottom: 6px;
}

.details strong {
  color: #8b3a3a;
}

/* ================= NOTE ================= */
.note {
  margin-top: 22px;
  font-size: 0.95rem;
  line-height: 1.6;
}

/* ================= FOOTER ================= */
.footer-text {
  margin-top: 40px;
  font-size: 1.1rem;
  color: #fff;
  text-shadow: 0 2px 6px rgba(0,0,0,0.25);
}

/* ================= MOBILE ================= */
@media (max-width: 768px) {
  .ticket {
    flex-direction: column;
  }

  .ticket-divider {
    height: 14px;
    width: 100%;
    background:
      repeating-linear-gradient(
        to right,
        transparent,
        transparent 10px,
        #f2b7a4 10px,
        #f2b7a4 18px
      );
  }

  .ticket-right {
    text-align: center;
  }
}
