/* ================= PAGE ================= */
.sunflower-page {
  min-height: 100vh;
  padding: 90px 20px 120px;
  background: linear-gradient(
    180deg,
    #fff6e0,
    #fffdf5
  );
  font-family: "Nunito", "Quicksand", sans-serif;
  text-align: center;
}

/* ================= TITLE ================= */
.sunflower-title {
  font-size: 3.2rem;
  font-family: "Dancing Script", cursive;
  color: #4b3621;
  margin-bottom: 12px;
}

.sunflower-subtitle {
  font-size: 1.1rem;
  color: #6b4a2d;
  max-width: 520px;
  margin: 0 auto 70px;
  line-height: 1.6;
}

/* ================= GRID ================= */
.sunflower-grid {
  max-width: 1000px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 60px;
}

/* ================= CARD ================= */
.sunflower-card {
  background: rgba(255, 253, 245, 0.96);
  border-radius: 26px;
  padding: 20px 20px 26px;
  box-shadow: 0 24px 48px rgba(0,0,0,0.18);
  transition: transform 0.35s ease;
}

.sunflower-card:hover {
  transform: translateY(-8px);
}

.sunflower-card img {
  width: 100%;
  height: 340px;
  object-fit: cover;
  border-radius: 20px;
  margin-bottom: 18px;
}

/* ================= CAPTION ================= */
.caption {
  font-size: 1rem;
  color: #4b3621;
  line-height: 1.5;
}

/* ================= MOBILE ================= */
@media (max-width: 768px) {
  .sunflower-title {
    font-size: 2.6rem;
  }

  .sunflower-card img {
    height: 300px;
  }
}
