/* ================= PAGE ================= */
.us-page {
  min-height: 100vh;
  padding: 80px 20px 120px;
  background: linear-gradient(
    180deg,
    #fff5e6,
    #fffdf5
  );
  font-family: "Nunito", "Quicksand", sans-serif;
}

/* ================= TITLE ================= */
.us-title {
  text-align: center;
  font-size: 3rem;
  margin-bottom: 80px;
  color: #4b3621;
  font-family: "Dancing Script", cursive;
}

/* ================= TIMELINE ================= */
.timeline {
  max-width: 1000px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 100px;
}

/* ================= MEMORY BLOCK ================= */
.memory {
  display: flex;
  align-items: center;
  gap: 60px;
}

/* alternate layout */
.memory.right {
  flex-direction: row-reverse;
}

/* ================= TEXT ================= */
.memory-text {
  flex: 1;
  background: rgba(255, 253, 245, 0.95);
  border-radius: 24px;
  padding: 36px 32px;
  box-shadow: 0 20px 40px rgba(0,0,0,0.12);
  position: relative;
}

.memory-text p {
  font-size: 1.15rem;
  line-height: 1.7;
  color: #4b3621;
}

/* subtle accent bar */
.memory-text::before {
  content: "";
  position: absolute;
  left: 0;
  top: 20%;
  width: 6px;
  height: 60%;
  background: #f4c430;
  border-radius: 3px;
}

/* ================= IMAGE ================= */
.memory-image {
  flex: 1;
  display: flex;
  justify-content: center;
}

.memory-image img {
  width: 100%;
  max-width: 420px;
  border-radius: 26px;
  box-shadow: 0 30px 60px rgba(0,0,0,0.25);
  object-fit: cover;
  transition: transform 0.35s ease;
}

.memory-image img:hover {
  transform: scale(1.04);
}

/* ================= MOBILE ================= */
@media (max-width: 768px) {
  .memory {
    flex-direction: column;
    gap: 30px;
  }

  .memory.right {
    flex-direction: column;
  }

  .memory-text::before {
    left: auto;
    right: 20px;
    top: -6px;
    width: 40%;
    height: 6px;
  }

  .us-title {
    font-size: 2.4rem;
  }
}
