/* ================= PAGE ================= */
.second-container {
  min-height: 100vh;
  padding: 90px 20px;

  /* WARM, DIM, CUTE DUSK */
  background:
    radial-gradient(
      ellipse at top,
      rgba(255, 215, 200, 0.45),
      rgba(0, 0, 0, 0) 60%
    ),
    linear-gradient(
      180deg,
      #e9c4b5,   /* soft peach */
      #d9b0a6,   /* blush clay */
      #c7a3a0    /* warm lavender shadow */
    );

  font-family: "Comic Sans MS", "Poppins", cursive;
  text-align: center;
  position: relative;
  overflow: hidden;
}

/* soft vignette (very light) */
.second-container::after {
  content: "";
  position: absolute;
  inset: 0;
  background: radial-gradient(
    ellipse at center,
    rgba(0,0,0,0) 55%,
    rgba(0,0,0,0.16) 100%
  );
  pointer-events: none;
}

/* ================= FLOATING BACKGROUND ================= */
.floating-bg img {
  position: absolute;
  width: 260px;
  opacity: 0.22;
  animation: floatUp 26s linear infinite;
  pointer-events: none;
  filter: drop-shadow(0 14px 30px rgba(0,0,0,0.25));
}

.floating-bg img:nth-child(1) { left: 12%; }
.floating-bg img:nth-child(2) { left: 50%; animation-duration: 30s; }
.floating-bg img:nth-child(3) { left: 78%; animation-duration: 28s; }

@keyframes floatUp {
  from { bottom: -220px; }
  to { bottom: 120%; }
}

/* ================= TITLE ================= */
.title {
  font-size: 2.7rem;
  margin-bottom: 60px;

  /* CUTE CREAMY TEXT */
  color: #fff7f2;
  text-shadow: 0 4px 14px rgba(140, 80, 80, 0.35);
}

/* ================= QUESTION CARDS ================= */
.question-card {
  /* CREAM + BLUSH (NOT WHITE) */
  background: linear-gradient(
    135deg,
    rgba(255, 245, 238, 0.96),
    rgba(255, 232, 226, 0.96)
  );
  opacity: 0.75;
  border-radius: 34px; /* extra cute */
  padding: 32px 30px;
  margin: 28px auto;
  max-width: 460px;

  box-shadow:
    0 20px 42px rgba(180, 110, 120, 0.28),
    inset 0 0 0 1.5px rgba(255, 210, 210, 0.55);

  position: relative;
  z-index: 1;
}

/* ================= TEXT ================= */
.question-card h3 {
  color: #5a3b34; /* warm cocoa */
  font-size: 1.4rem;
  font-weight: 700;
}

.subtitle {
  color: #7a5550;
  margin-bottom: 16px;
  font-size: 1.05rem;
}

/* ================= EFFECT VARIANTS ================= */
.glow {
  box-shadow:
    0 0 30px rgba(255, 170, 170, 0.35),
    inset 0 0 0 1.5px rgba(255, 210, 210, 0.6);
}

.bounce {
  animation: softBounce 1.7s ease-in-out infinite;
}

@keyframes softBounce {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-6px); }
}

.locked {
  opacity: 0.9;
  background: linear-gradient(
    135deg,
    rgba(255, 238, 230, 0.95),
    rgba(245, 220, 215, 0.95)
  );
}

/* ================= BUTTON ================= */
.finish-btn {
  margin-top: 60px;
  padding: 18px 64px;
  font-size: 22px;

  border-radius: 999px;
  border: none;
  cursor: pointer;
  font-weight: 800;

  /* CUTE WARM BUTTON */
  background: linear-gradient(
    135deg,
    #f3b6b6,
    #e79ca0
  );
  color: #5a2c2c;

  box-shadow: 0 18px 36px rgba(180, 90, 100, 0.35);
  transition: all 0.25s ease;
}

.finish-btn:hover {
  transform: translateY(-4px) scale(1.06);
  box-shadow: 0 24px 46px rgba(180, 90, 100, 0.45);
}
