/* ================= PAGE ================= */
.end-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #ffc3a0, #ffa5b0);
  background-size: 300% 300%;
  animation: dreamyGradient 16s ease infinite;
  font-family: "Comic Sans MS", "Poppins", cursive;
  text-align: center;
  padding-top: 50px;
  overflow: hidden;
}

@keyframes dreamyGradient {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* ================= HEADER ================= */
.end-header {
  font-size: 3.2rem;
  color: white;
  margin-bottom: 70px;
  animation: fadeInDown 1.2s ease;
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ================= ICON LAYOUT ================= */
.icon-layout {
  display: flex;
  flex-direction: column;
  gap: 90px;
}

.icon-row {
  display: flex;
  justify-content: center;
  gap: 160px;
}

.icon-row.center {
  margin-top: 30px;
}

/* ================= FLOATING ICON ================= */
.floating-icon {
  animation: bob 5s ease-in-out infinite;
  cursor: pointer;
}

/* gentle float */
@keyframes bob {
  0% { transform: translateY(0); }
  50% { transform: translateY(-22px); }
  100% { transform: translateY(0); }
}

/* ================= IMAGE ================= */
.floating-image {
  width: 450px; /* VERY BIG */
  height: auto;
  filter: drop-shadow(0 30px 55px rgba(0, 0, 0, 0.28));
  transition: transform 0.35s ease, filter 0.35s ease;
  user-select: none;
  pointer-events: none;
}

/* BIG FINAL ICON */
.floating-icon.big .floating-image {
  width: 700px;
}

/* hover */
.floating-icon:hover .floating-image {
  transform: scale(1.1);
  filter: drop-shadow(0 45px 80px rgba(0, 0, 0, 0.38));
}
