/* ================= PAGE ================= */
.love-letter-page {
  min-height: 100vh;
  background: radial-gradient(
    circle at top,
    #ffd1dc,
    #ffc0cb,
    #ffb6c1
  );
  display: flex;
  justify-content: center;
  align-items: flex-start;
  font-family: "Nunito", sans-serif;
  padding: 90px 20px 70px;
  position: relative;
  overflow: hidden;
}

/* ================= FLOATING HEARTS ================= */
.hearts-bg {
  position: absolute;
  inset: 0;
  pointer-events: none;
  overflow: hidden;
}

.heart {
  position: absolute;
  bottom: -10%;
  color: rgba(255, 90, 120, 0.35);
  animation: floatHeart linear infinite;
}

@keyframes floatHeart {
  from {
    transform: translateY(0) scale(0.8);
    opacity: 0;
  }
  to {
    transform: translateY(-120vh) scale(1.4);
    opacity: 1;
  }
}

/* ================= CONTENT ================= */
.letter-content {
  position: relative;
  z-index: 2;
  max-width: 760px;
  width: 100%;
}

/* ================= TITLE ================= */
.letter-title {
  font-family: "Dancing Script", cursive;
  font-size: 3.4rem;
  color: #ffffff;
  text-align: center;
  margin-bottom: 36px;
  text-shadow: 0 4px 10px rgba(0,0,0,0.25);
}

/* ================= LETTER TEXT ================= */
.letter-text {
  font-family: "Handlee", cursive;
  font-size: 1.35rem;            /* BIGGER */
  font-weight: 700;              /* BOLD */
  line-height: 2;
  color: #c7375f;                /* ❤️ lovely blood-red */
  white-space: pre-line;
  text-align: center;
}

/* ================= FOOTER ================= */
.letter-footer {
  margin-top: 46px;
  text-align: center;
  font-size: 1.2rem;
  color: #ffffff;
  text-shadow: 0 2px 6px rgba(0,0,0,0.25);
}

/* ================= MOBILE ================= */
@media (max-width: 768px) {
  .letter-title {
    font-size: 2.6rem;
  }

  .letter-text {
    font-size: 1.15rem;
    line-height: 1.85;
  }
}
