/* ===== Page Container ===== */
.container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  background:
    radial-gradient(
      ellipse at top,
      rgba(255, 225, 215, 0.5),
      rgba(0, 0, 0, 0) 60%
    ),
    linear-gradient(
      180deg,
      #e9c4b5,
      #d9b0a6,
      #c7a3a0
    );

  font-family: "Comic Sans MS", "Poppins", cursive;
  overflow: hidden;
  text-align: center;
  position: relative;
}

/* gentle vignette */
.container::after {
  content: "";
  position: absolute;
  inset: 0;
  background: radial-gradient(
    ellipse at center,
    rgba(0,0,0,0) 60%,
    rgba(0,0,0,0.14) 100%
  );
  pointer-events: none;
}

/* ===== BIG FLOATING STICKER ===== */
.sticker {
  width: 350px;
  max-width: 90vw;
  margin-bottom: 28px;

  animation: floatSticker 5.2s ease-in-out infinite;
  z-index: 1;
}

/* cute glow behind sticker */
.sticker::after {
  content: "";
  position: absolute;
  inset: -18px;
  border-radius: 50%;
  background: radial-gradient(
    circle,
    rgba(255, 210, 200, 0.45),
    rgba(0,0,0,0) 70%
  );
  z-index: -1;
}

.sticker img {
  width: 100%;
  height: auto;
  display: block;
  pointer-events: none;
  user-select: none;

  filter: drop-shadow(0 22px 38px rgba(120, 70, 60, 0.45));
}

/* float with personality */
@keyframes floatSticker {
  0%   { transform: translateY(0) rotate(0deg); }
  40%  { transform: translateY(-14px) rotate(0.8deg); }
  70%  { transform: translateY(-10px) rotate(-0.8deg); }
  100% { transform: translateY(0) rotate(0deg); }
}

/* ===== TEXT ===== */
h1 {
  font-size: 2.45rem;
  margin-bottom: 32px;
  font-weight: 700;

  color: #fff7f2;
  text-shadow:
    0 4px 14px rgba(140, 80, 80, 0.35),
    0 0 8px rgba(255, 210, 200, 0.35);

  animation: textFadeIn 1.2s ease;
}

@keyframes textFadeIn {
  from { opacity: 0; transform: translateY(-6px); }
  to   { opacity: 1; transform: translateY(0); }
}

/* ===== BUTTON WRAPPER ===== */
.buttons {
  position: relative;
  display: flex;
  gap: 34px;
  z-index: 1;
}

/* ===== BASE BUTTON ===== */
button {
  padding: 18px 54px;
  font-size: 22px;
  border-radius: 999px;
  border: none;
  cursor: pointer;
  font-weight: 700;
  transition: all 0.25s ease;
}

/* ===== YES BUTTON (CUTER) ===== */
.yes-btn {
  background: linear-gradient(
    135deg,
    #e8caa8,
    #d6b48d
  );
  color: #4b3a2c;

  box-shadow:
    0 14px 32px rgba(150, 110, 70, 0.35),
    inset 0 0 0 1px rgba(255, 235, 210, 0.5);
}

/* subtle sparkle on hover */
.yes-btn:hover {
  transform: translateY(-5px) scale(1.06);
  box-shadow:
    0 22px 44px rgba(150, 110, 70, 0.45),
    0 0 14px rgba(255, 220, 190, 0.45);
}

/* tiny squish */
.yes-btn:active {
  transform: scale(0.95);
}

/* forced yes celebration */
.yes-btn.forced {
  animation: happyBounce 0.45s ease infinite alternate;
}

@keyframes happyBounce {
  from { transform: scale(1); }
  to   { transform: scale(1.12); }
}

/* ===== NO BUTTON (ADORABLY EVIL) ===== */
.no-btn {
  background: linear-gradient(
    135deg,
    #f2b8b8,
    #e6a2a6
  );
  color: #5a2c2c;
  position: relative;

  box-shadow:
    0 14px 32px rgba(180, 90, 100, 0.35),
    inset 0 0 0 1px rgba(255, 220, 220, 0.45);
}

.no-btn:hover {
  transform: translateY(-4px) rotate(-2deg);
  box-shadow:
    0 20px 42px rgba(180, 90, 100, 0.45);
}

/* ===== SHAKE ===== */
@keyframes shake {
  0% { transform: translateX(0); }
  20% { transform: translateX(10px); }
  40% { transform: translateX(-10px); }
  60% { transform: translateX(10px); }
  80% { transform: translateX(-10px); }
  100% { transform: translateX(0); }
}

.shake {
  animation: shake 0.45s;
}

/* ===== CONFETTI ===== */
.confetti {
  position: absolute;
  top: 20px;
  font-size: 42px;
  animation: confettiPop 0.7s ease forwards;
}

@keyframes confettiPop {
  from { transform: scale(0); opacity: 0; }
  to   { transform: scale(1.4); opacity: 1; }
}
